  padding: 0;
  margin: 0;
}

body {
  font-family: "Lora", serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.container {
  display: grid;
  grid-template-columns: 1fr;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  margin-bottom: 30px;
}

.image-container {
  margin-top: 15px;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.text-container {
  padding: 33px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.text-container h1 {
  text-align: center;
  color: #000000;
  margin: 0;
  font-size: 30px;
}

.text-container p {
  margin: 10px 0 0;
  font-size: 22px;
  line-height: 1.5;
}

/*
    AppBar
  */

.app-bar {
  padding: 8px 16px;
  background-color: #31363F;  
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.logo {
  background-size: cover;
  background-position: center;     
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand .name_brand {
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
  user-select: none;
  width: 20%;
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  text-align: left;
  line-height: 24px;
}


.app-bar .app-bar__navigation {
  line-height: 44px;
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: #31363F;
  overflow: hidden;
}
.hamburgerButton {
  font-weight: bold;
  width: 50%;
  margin-bottom: 10px;
  display: inline-block;
  width: 44px;
  min-height: 44px;
  line-height: 44px;
  text-decoration: none;
  color: white !important;
}
.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
}
.jumbotron {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-position: center;
}
.hero {
  margin: auto;
  max-width: 900px;
}
h1 {
  color: #fff;
}
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #3c5b6f;
  color: white;
  padding: 8px;
  z-index: 1000;
}

.skip-link:focus {
  top: 0;
}

/*
    Main Content
  */

main {
  padding: 32px;
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.jelajah {
  margin-bottom: 90px;
}

.content .content__heading {
  --tw-text-opacity: 1;
  color: rgb(55 65 81 / var(--tw-text-opacity));
  text-align: center;
  font-weight: bold;
  font-size: 3rem;
  line-height: 1;
}

/*
    foods
  */

.foods {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
  margin: 32px 0;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}
/*
    Movie Item
  */
 
.food-item {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  width: 100%;
  height: auto; 
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.food-item:hover,
.food-item:focus-visible {
  scale: 1.05;
  box-shadow: 0 6px 6px 6px #48373777;
}
.food_city {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 7px;
}
.food-item__header {
  position: relative;
}

.food-item .food-item__header .food-item__header__poster {
  width: 100%;
}

.food-item .food-item__header .food-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.food-item
  .food-item__header
  .food-item__header__rating
  .food-item__header__rating__score {
  margin-left: 10px;
}

.food-item .food-item__content {
  padding: 16px;
}

.food-item .food-item__content h3 {
  margin: 0 0 10px 0;
  min-width: 44px;
  min-height: 44px;

}

.food-item .food-item__content h3 a {
  color: black;
  font-weight: 300;
  font-style : italic;
  text-decoration: none;
  min-width: 44px;
  min-height: 44px;
  font-size: 31px;
}
.food-item .food-item__content h3 a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 44px;
  height: 44px;
}

.food-item .food-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

/*
    food
  */

.food {
  margin-left: 0;
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
}

.food .food__poster {
  width: 100%;
  /* max-width: 800px;
  max-height: 800px; */
}

.food .food__info h4 {
  margin: 8px 0;
}

/* Detail Review */
.detail-coment {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
  width: 100%;
  max-width: 800px;
  font-size: 1em;
  text-align: center;
}

/* .detail-coment-item {
  color: var(--secondary-color);
  background-color: var(--third-color);
  margin-bottom: 1.5em;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  border-radius: 2em;
  /* justify-content: center; */
  /* text-align: center; */
  /* padding: 3px; */
/* } */

.header-coment {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em;
  color: var(--secondary-color);
  border-bottom: 1px solid var(--secondary-color);
}

.bahan-coment {
  font-weight: bold;
  font-size: 1.5em;
  margin: 0;
}
/* .teks-coment {
  margin-top: 20px;
  text-align: center;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 800;
} */
.coment-bahan {
  font-size: 1em;
  font-weight: lighter;
  font-size: 1em;
}

.body-coment {
  margin-top: 20px;
  padding: 1em;
  text-align: left;
  font-size: 1.37em;
}

.list-menu-teks {
  margin-top: 20px;
  margin-bottom: 15px;
}
/*
    Form review
  */
.form-coment {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
}

/* Styling form labels */
.form-label {
  font-weight: bold;
}

/* Styling form inputs */
.form-control {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  margin-bottom: 15px;
  min-height: 44px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}

/* Styling form submit button */
.btn {
  height: 44px;
  padding: 10px 20px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* Hover effect for submit button */
.btn:hover {
  background-color: #45a049;
}



.desc{
  box-shadow: 5px 5px 10px 1px #000000;
}

.tentang{
  animation: wipe-cinematic-in 2s ease 0s 1 normal forwards;
}
@keyframes wipe-cinematic-in {
  0% {
    clip-path: inset(100% 0 100% 0);
  }
  30%, 70% {
    clip-path: inset(10% 0 10% 0);
  }
  100% {
    clip-path: inset(0 0 0 0);
  }
}

[transition-style="in:wipe:cinematic"] {
   animation-name: wipe-cinematic-in;
}


.jelajah{
  animation: wipe-in-top-right 2s ease 0s 1 normal forwards;
}
@keyframes wipe-in-top-right {
  from {
    clip-path: polygon(-50% 50%, 50% 150%, 50% 150%, -50% 50%);
  }
  to {
    clip-path: polygon(50% -50%, 150% 50%, 50% 150%, -50% 50%);
  }
}

[transition-style="in:wipe:top-right"] {
  animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-top-right both;
}
