line-height: 44px;\n  text-decoration: none;\n  color: white !important;\n}\n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n\n.app-bar .app-bar__navigation ul li a {\n  display: inline-block;\n  text-decoration: none;\n  color: #fff;\n  font-size: 20px;\n  font-weight: 700;\n  padding: 8px;\n  margin-bottom: 5px;\n  width: 100%;\n}\n.jumbotron {\n  display: flex;\n  align-items: center;\n  min-height: 380px;\n  width: 100%;\n  text-align: center;\n  background-position: center;\n}\n.hero {\n  margin: auto;\n  max-width: 900px;\n}\nh1 {\n  color: #fff;\n}\n.skip-link {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: #3c5b6f;\n  color: white;\n  padding: 8px;\n  z-index: 1000;\n}\n\n.skip-link:focus {\n  top: 0;\n}\n\n/*\n    Main Content\n  */\n\nmain {\n  padding: 32px;\n  flex: 1;\n}\n\n.content {\n  margin: 0 auto;\n  min-height: 100%;\n}\n\n.jelajah {\n  margin-bottom: 90px;\n}\n\n.content .content__heading {\n  --tw-text-opacity: 1;\n  color: rgb(55 65 81 / var(--tw-text-opacity));\n  text-align: center;\n  font-weight: bold;\n  font-size: 3rem;\n  line-height: 1;\n}\n\n/*\n    foods\n  */\n\n.foods {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 30px;\n  margin: 32px 0;\n}\n\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #db0000;\n\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n\n  font-size: 18px;\n  color: white;\n  cursor: pointer;\n}\n/*\n    Movie Item\n  */\n \n.food-item {\n  border: 1px solid #ccc;\n  border-radius: 8px;\n  overflow: hidden;\n  width: 100%;\n  height: auto; \n  text-align: center;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  background-color: #fff;\n}\n\n.food-item:hover,\n.food-item:focus-visible {\n  scale: 1.05;\n  box-shadow: 0 6px 6px 6px #48373777;\n}\n.food_city {\n  font-size: 20px;\n  font-weight: 700;\n  margin-bottom: 7px;\n}\n.food-item__header {\n  position: relative;\n}\n\n.food-item .food-item__header .food-item__header__poster {\n  width: 100%;\n}\n\n.food-item .food-item__header .food-item__header__rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  left: 0;\n  display: inline-block;\n  background-color: black;\n  color: white;\n}\n\n.food-item\n  .food-item__header\n  .food-item__header__rating\n  .food-item__header__rating__score {\n  margin-left: 10px;\n}\n\n.food-item .food-item__content {\n  padding: 16px;\n}\n\n.food-item .food-item__content h3 {\n  margin: 0 0 10px 0;\n  min-width: 44px;\n  min-height: 44px;\n\n}\n\n.food-item .food-item__content h3 a {\n  color: black;\n  font-weight: 300;\n  font-style : italic;\n  text-decoration: none;\n  min-width: 44px;\n  min-height: 44px;\n  font-size: 31px;\n}\n.food-item .food-item__content h3 a::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 44px;\n  height: 44px;\n}\n\n.food-item .food-item__content p {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4; /* number of lines to show */\n  -webkit-box-orient: vertical;\n}\n\n/*\n    food\n  */\n\n.food {\n  margin-left: 0;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 16px;\n}\n\n.food .food__poster {\n  width: 100%;\n  /* max-width: 800px;\n  max-height: 800px; */\n}\n\n.food .food__info h4 {\n  margin: 8px 0;\n}\n\n/* Detail Review */\n.detail-coment {\n  display: grid;\n  grid-template-columns: 1fr 1fr 1fr;\n  gap: 20px;\n  width: 100%;\n  max-width: 800px;\n  font-size: 1em;\n  text-align: center;\n}\n\n/* .detail-coment-item {\n  color: var(--secondary-color);\n  background-color: var(--third-color);\n  margin-bottom: 1.5em;\n  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,\n    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n  border-radius: 2em;\n  /* justify-content: center; */\n  /* text-align: center; */\n  /* padding: 3px; */\n/* } */\n\n.header-coment {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 0.5em;\n  color: var(--secondary-color);\n  border-bottom: 1px solid var(--secondary-color);\n}\n\n.bahan-coment {\n  font-weight: bold;\n  font-size: 1.5em;\n  margin: 0;\n}\n/* .teks-coment {\n  margin-top: 20px;\n  text-align: center;\n  margin-bottom: 15px;\n  font-size: 18px;\n  font-weight: 800;\n} */\n.coment-bahan {\n  font-size: 1em;\n  font-weight: lighter;\n  font-size: 1em;\n}\n\n.body-coment {\n  margin-top: 20px;\n  padding: 1em;\n  text-align: left;\n  font-size: 1.37em;\n}\n\n.list-menu-teks {\n  margin-top: 20px;\n  margin-bottom: 15px;\n}\n/*\n    Form review\n  */\n.form-coment {\n  margin-top: 20px;\n  padding: 20px;\n  border: 1px solid #ccc;\n  border-radius: 8px;\n  background-color: #f9f9f9;\n}\n\n/* Styling form labels */\n.form-label {\n  font-weight: bold;\n}\n\n/* Styling form inputs */\n.form-control {\n  width: 100%;\n  padding: 10px;\n  margin-top: 5px;\n  margin-bottom: 15px;\n  min-height: 44px;\n  border: 1px solid #ccc;\n  border-radius: 5px;\n  box-sizing: border-box;\n}\n\n/* Styling form submit button */\n.btn {\n  height: 44px;\n  padding: 10px 20px;\n  background-color: #4caf50;\n  color: white;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  transition: background-color 0.3s;\n}\n\n/* Hover effect for submit button */\n.btn:hover {\n  background-color: #45a049;\n}\n\n\n\n.desc{\n  box-shadow: 5px 5px 10px 1px #000000;\n}\n\n.tentang{\n  animation: wipe-cinematic-in 2s ease 0s 1 normal forwards;\n}\n@keyframes wipe-cinematic-in {\n  0% {\n    clip-path: inset(100% 0 100% 0);\n  }\n  30%, 70% {\n    clip-path: inset(10% 0 10% 0);\n  }\n  100% {\n    clip-path: inset(0 0 0 0);\n  }\n}\n\n[transition-style=\"in:wipe:cinematic\"] {\n   animation-name: wipe-cinematic-in;\n}\n\n\n.jelajah{\n  animation: wipe-in-top-right 2s ease 0s 1 normal forwards;\n}\n@keyframes wipe-in-top-right {\n  from {\n    clip-path: polygon(-50% 50%, 50% 150%, 50% 150%, -50% 50%);\n  }\n  to {\n    clip-path: polygon(50% -50%, 150% 50%, 50% 150%, -50% 50%);\n  }\n}\n\n[transition-style=\"in:wipe:top-right\"] {\n  animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-top-right both;\n}\n"],"sourceRoot":""}]);
